@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;800&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  box-sizing: border-box;
}

*:focus, *:active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: none;
  -webkit-overflow-scrolling: touch;
}

html {
  overflow-x: hidden;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
  background: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

:root {
  --white: rgba(255,255,255,1);
  --black: rgba(0, 0, 0, 1);
  --blue: rgba(36, 50, 133, 1);
  --blue-light: rgba(126, 131, 166, 1);
  --blue-light2: rgba(134, 139, 172, 1);
  --blue-lightness: rgba(241, 243, 253, 1);
  --orange: rgba(250, 96, 32, 1);
  --gray: rgba(182, 183, 187, 1);
  --border: rgba(234, 236, 249, 1);
}

* {
  font-family: Montserrat, sans-serif;
}

*::-webkit-scrollbar {
  width: 6px;
  background-color: var(--black);
}

*::-webkit-scrollbar-thumb {
  border-radius: 6px;
  background-color: var(--yellow);
}

*, a {
  color: var(--black);
}

p {
  font-weight: 500;
  font-size: 15px;
  line-height: 24px;
}

.fix-box {
  height: 100%;
  width: 290px;
  min-width: 290px;
  max-width: 1920px;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 768px) {
  .fix-box {
    width: 708px;
  }
}

@media (min-width: 1200px) {
  .fix-box {
    width: 1170px;
  }
}

@media (min-width: 1440px) {
  .fix-box {
    width: 1230px;
  }
}
